<template>
    <div class="container">
        <p class="countdown">
            {{number}}
        </p>
        <p class="text">
            {{text}}
        </p>
    </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import {GetHealthResult, GetHealthStage} from "vue-vital-sign-camera";
import {PropType} from "vue";
export default defineComponent({
    setup() {
        
    },
    props: {
        healtResult: {
            type: Object as PropType<GetHealthResult>
        },
    },
    computed: {
        number() {
            if(!this.healtResult) {
                return -1;
            }
            return this.healtResult.remainingTime.toFixed(0);
        },
        text() {
            let text = '';
            switch(this.healtResult?.stage) {
                case GetHealthStage.CollectingData: 
                    text = "Scanning...";
                    break;
                case GetHealthStage.AnalyzingData:
                    text = "Analyzing...";
                    break;
                case GetHealthStage.WaitingData:
                    text = "Loading...";
                    break;
            }
            return text;
        }
    }
})
</script>

