<template>
    <div class="container" ref="container">
        <div :style="containerStyle">
            <div :style="bboxStyle"></div>
        </div>
    </div>
</template>

<script lang="ts">
import {NormalizedFacebox, VitalSignCamera} from "vue-vital-sign-camera";
import { CSSProperties, defineComponent } from 'vue';
import adapter from 'webrtc-adapter';
import {PropType} from "vue";

export default defineComponent ({
    props: {
        facebox: Object as PropType<NormalizedFacebox>,
        cameraIndex: Number
    },
    setup() {

    },
    mounted() {
        this.changeTransform();
    },
    data() {
        return{
            width: 0,
            height: 0,
            left: 0,
            top: 0,
            videoWidth: 0,
            videoHeight: 0,
            videoOffsetTop: 0,
            videoOffsetLeft: 0,
            additionalWidth: 0,
            additionalHeight: 0,
            containerAspectRatio: 0,
            aspectRatio: 0,
            transform: "scaleX(-1)"
        }
    },
    watch: {
        facebox() {
            const video = (document.getElementsByTagName('video')[0].srcObject as MediaStream).getVideoTracks()[0];
            var width, height;
            if(video){
                height = video.getSettings().height as number;
                width = video.getSettings().width as number;
                this.aspectRatio = width && height ? width/height : 0;
            }
            if(!this.facebox || !this.$parent) {
                return;
            }
            const container = (this.$parent.$refs.vscam as InstanceType<typeof VitalSignCamera>).$refs.video as HTMLVideoElement;
            this.containerAspectRatio = container.clientWidth/container.clientHeight;
            
            if(this.containerAspectRatio < this.aspectRatio) {
                this.videoOffsetLeft = (this.containerAspectRatio - this.aspectRatio) / this.containerAspectRatio / 2 * 100;
                this.videoWidth = (100+Math.abs(this.videoOffsetLeft)*2);
                this.videoOffsetTop = 0;
                this.videoHeight = 100;
            } else {
                this.videoOffsetTop = (1/this.containerAspectRatio - 1/this.aspectRatio) * this.containerAspectRatio/2*100;
                this.videoHeight = (100+Math.abs(this.videoOffsetTop)*2);
                this.videoOffsetLeft = 0;
                this.videoWidth = 100;
            }
            
            if (height && width) {
                this.width = this.facebox.width * 100;
                this.height = this.facebox.height * 100;
                this.left = this.facebox.xCenter * 100 - this.width/2;
                this.top = this.facebox.yCenter * 100 - this.height/2;
            }
        },
        cameraIndex() {
            this.changeTransform();
        },
    },
    methods: {
        changeTransform() {
            setTimeout(() => {            
                const browser = adapter.browserDetails.browser;
                var facingMode;
                if(browser == 'firefox') {
                    facingMode = (document.getElementsByTagName('video')[0].srcObject as MediaStream)?.getVideoTracks()[0].getSettings().facingMode;
                }
                else {
                    facingMode = (document.getElementsByTagName('video')[0].srcObject as MediaStream)?.getVideoTracks()[0].getCapabilities().facingMode;
                }
                if(facingMode && facingMode[0] === 'environment') {
                    this.transform = 'none';
                } else {
                    this.transform = 'scaleX(-1)';
                }
            }, 1000);
        }
    },
    computed: {
        containerStyle(){
            const style = {
                position: 'absolute',
                left: this.videoOffsetLeft + '%',
                top: this.videoOffsetTop + '%',
                width:this.videoWidth + "%",
                height:this.videoHeight +"%", 
                aspectRatio: this.aspectRatio,
                transform: this.transform
            }
            return style as CSSProperties;
        },
        bboxStyle() {
            const style = {
                position:"absolute", 
                borderStyle: "solid", 
                borderWidth: "8px",
                borderColor: "#198754",
                left: this.left + '%', 
                top: this.top + '%', 
                width: this.width + '%',
                height: this.height + '%'
            }
            return style as CSSProperties;
        }
    },
})
</script>

<style scoped>
.container {
    overflow: hidden;
    position: absolute;
    width: 100%;
    height: 100%;
}
</style>