<template>
  <div class="face-mesh-container" ref="container">
    <canvas ref='canvas' :style="style"/>
  </div>
</template>

<script lang="ts">
import { drawConnectors, NormalizedLandmarkList } from '@mediapipe/drawing_utils';
import { FACEMESH_TESSELATION } from '@mediapipe/face_mesh';
import { defineComponent } from 'vue';
import adapter from "webrtc-adapter";
import { PropType } from "vue";
import { VitalSignCamera } from 'vue-vital-sign-camera';

export default defineComponent({
  name: 'face-mesh-view',
  props: {
    'landmarks': Object as PropType<NormalizedLandmarkList>,
    cameraIndex: Number
  },
  setup() {
    return {
      style: {}
    }
  },
  data() {
    return {
      transform: "scaleX(-1)"
    }
  },
  mounted() {
    this.changeTransform;
  },
  watch: {
    landmarks() {
      this.updateStyle()
      this.redraw()
    },
    aspectRatio() {
      this.updateStyle();
      this.redraw();
    },
    cameraIndex() {
      this.changeTransform();
    }
  },
  methods: {
    changeTransform() {
      setTimeout(() => {                
          const browser = adapter.browserDetails.browser;
          var facingMode;
          if(browser == 'firefox') {
          facingMode = (document.getElementsByTagName('video')[0].srcObject as MediaStream)?.getVideoTracks()[0].getSettings().facingMode;
          }
          else {
          facingMode = (document.getElementsByTagName('video')[0].srcObject as MediaStream)?.getVideoTracks()[0].getCapabilities().facingMode;
          }
          if(facingMode && facingMode[0] === 'environment') {
              this.transform = 'none';
          } else {
              this.transform = 'scaleX(-1)';
          }
      }, 1000);
    },
    updateStyle() {
      const video = (document.getElementsByTagName('video')[0].srcObject as MediaStream).getVideoTracks()[0];
      var aspectRatio = 0;
      if(video){
          const height = video.getSettings().height;
          const width = video.getSettings().width;
          aspectRatio = width && height ? width/height : 0;
      }
      let style = {
        width: undefined as string|undefined,
        height: undefined as string|undefined,
        left: '0%',
        top: '0%',
        aspectRatio: aspectRatio
      }
      const container = (this.$parent?.$refs.vscam as InstanceType<typeof VitalSignCamera>).$refs.video as HTMLVideoElement;
      let containerAspectRatio = container.clientWidth/container.clientHeight
      if (containerAspectRatio < aspectRatio) {
        const offset = (containerAspectRatio-aspectRatio)/containerAspectRatio/2*100
        style.left = `${offset}%`
        style.width = `${ (-offset)*2 + 100 }%`
      } else {
        style.top = `${ (1/containerAspectRatio-1/aspectRatio )*containerAspectRatio/2*100 }%`
        style.width = '100%'
      }
      this.style = style;
    },
    redraw() {
      let canvas = this.$refs.canvas as HTMLCanvasElement
      canvas.width = 640
      canvas.height = 480
      const ctx = canvas.getContext('2d');

      if(ctx !== null) {
        ctx.save() 
        ctx.clearRect(0, 0, canvas.width, canvas.height)
        drawConnectors(
          ctx,
          this.landmarks,
          FACEMESH_TESSELATION,
          {
            color: "lightgreen",
            lineWidth: .2,
          }
        )
        ctx.restore()
      }
    }
  },
})
</script>

<style scoped>
@import './face-mesh-view.css';

.face-mesh-container {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 0;
  overflow: hidden;
}

canvas {
  -webkit-transform: v-bind(transform);
  transform: v-bind(transform);
  position: absolute;
}


</style>