<template>
    <div class="container" v-if="showResult">
        <div class="close" @click="hideResult">[x]</div>
        <div class="section">Vital Signs</div>
        <vital-signs :name="'Heart Rate'" :value="vs?.heartRate"/>
        <vital-signs :name="'SPO2'" :value="vs?.spo2"/>
        <vital-signs :name="'IBI'" :value="vs?.ibi"/>
        <vital-signs v-if="vs?.stress" :name="'Stress'" :value="vs.stress"/>
        <vital-signs v-if="vs?.respiratoryRate" :name="'Respiratory Rate'" :value="vs.respiratoryRate"/>
        <vital-signs v-if="vs?.hrvRmssd" :name="'HRV RMSSD'" :value="vs.hrvRmssd"/>
        <vital-signs v-if="vs?.hrvSdnn" :name="'HRV SDNN'" :value="vs.hrvSdnn"/>
        <vital-signs v-if="vs?.temperature" :name="'Temperature'" :value="vs.temperature"/>
        <vital-signs v-if="vs?.bloodPressure" :name="'BloodPressure'" :value="vs.bloodPressure"/>
        <vital-signs v-if="vs?.bloodPressureSystolic" :name="'Blood Pressure Systolic'" :value="vs.bloodPressureSystolic"/>
        <vital-signs v-if="vs?.bloodPressureDiastolic" :name="'Blood Pressure Diastolic'" :value="vs.bloodPressureDiastolic"/>
        <vital-signs v-if="vs?.facialSkinAge" :name="'Facial Skin Age'" :value="vs.facialSkinAge"/>
        <vital-signs v-if="vs?.bloodAlcohol" :name="'Blood Alcohol'" :value="vs.bloodAlcohol"/>
        <vital-signs v-if="vs?.bloodSugar" :name="'Blood Sugar'" :value="vs.bloodSugar"/>
        
        <div class="section">Holistic Analysis</div>
        <vital-signs :name="'BMI'" :value="holistic?.bmi"/>
        <vital-signs v-if="holistic?.generalWellness" :name="'General Wellness'" :value="holistic.generalWellness"/>
        
        <div class="section" v-if="cardio">
            <div class="section">Cardiovascular Risk</div>
            <vital-signs :name="'General'" :value="cardio.generalRisk"/>
            <vital-signs :name="'Congestive Heart Failure'" :value="cardio.congestiveHeartFailure"/>
            <vital-signs :name="'Coronary Heart Disease'" :value="cardio.coronaryHeartDisease"/>
            <vital-signs :name="'Intermittent Claudification'" :value="cardio.intermittentClaudication"/>
            <vital-signs :name="'Stroke'" :value="cardio.stroke"/>
        </div>

        <div class="section" v-if="covid">
            <div class="sec">Covid</div>
            <vital-signs :name="'Risk'" :value="covid.covidRisk"/>
        </div>

        <div class="section" v-if="healthResult?.scanParameters">
            <div class="sec">Scan Parameters</div>
            <vital-signs :name="'Signal Quality'" :value="healthResult.scanParameters.signalQuality"/>
        </div>
    </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import VitalSigns from "./VitalSigns.vue";
import {PropType} from "vue";
import { Health } from 'vue-vital-sign-camera';

export default defineComponent( {
    setup() {
        
    },
    emits: ['hideResult'],
    props: {
        healthResult: Object as PropType<Health>,
        showResult: Boolean,
    },
    components: {
        VitalSigns
    },
    computed: {
        vs() {
            return this.healthResult?.vitalSigns;
        },
        holistic() {
            return this.healthResult?.holisticHealth;
        },
        cardio() {
            return this.healthResult?.risks?.cardiovascularRisks;
        },
        covid() {
            return this.healthResult?.risks?.covidRisk;
        }
    },
    methods: {
        hideResult() {
            this.$emit('hideResult');
        }
    }
})
</script>

<style scoped>
.container {
    position: absolute;
    top: 40px;
    left: 15px;
    right: 15px;
    height: calc(100vh - 75px);
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
    background-color: rgba(0, 0, 0, 0.5);
    overflow: scroll;
}

.section {
    color: orange;
    margin-bottom: 5px;
    font-weight: bold;
    margin-top: 15px;
}

.close {
    text-align: right;
    color: white;
    padding-right: 5px;
    padding-top: 10px;
    cursor: pointer;
}

